@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap&effect=anaglyph);
@import url(https://fonts.googleapis.com/css?family=Courgette|Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Manrope&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: black;
  /* text-shadow: 2px 2px 2px #aaa;  */
  background-image: url(/static/media/rocks.61c816e9.jpg);
  background-position: center;
}

#carouselborder {
  position: relative;
  border: 49px solid;
  border-style: groove;
  border-color: rgb(100, 99, 99);
  /* border-image-source: linear-gradient(45deg, black, black , grey);
  border-image-slice: 1;  */
  box-shadow: 10px 5px 5px black;
} 

#heightpic {
  padding-top: 15px;
  height: auto;
  width: 100%;
  border-radius: 2% / 2%;
}

i {
  background: #007aff;
  color: white;
  padding: 11px;
  border-radius: 50%;
}

#jpic {
  border-radius: 25%;
  background: -webkit-linear-gradient(left top, #610241 0%, #f1069f 100%);
  padding: 10px;
  border-image-slice: 1;
}@media only screen and (max-width: 700px) {
  #jpic {
    display: none;
   }
 }

 @media only screen and (max-width: 700px) {
  #goneText {
    display: none;
   }
 }


.jumbotron {
  margin: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(45deg,black,black,grey, white, grey,black,black, black);
  background-size: 600% 100%;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation: gradient 16s linear infinite;
          animation: gradient 16s linear infinite;
  color: whitesmoke;
  display: block;
  font-weight: bold;
  font-family: 'Courgette', cursive;
}
@-webkit-keyframes gradient {
  0% {background-position: 0%}
  100% {background-position: 100%}
}
@keyframes gradient {
  0% {background-position: 0%}
  100% {background-position: 100%}
}

#modalbody {
  color: #9df5cc;
  background-image: linear-gradient(to bottom,  #610241,#f1069f, #610241);
}

.navbar {
  background-image: linear-gradient(#610241, #c40484);
  border-bottom: 7px solid pink;
  font-family:'Dancing Script', cursive, 'font-effect-anaglyph';
  font-size: 25px;
}

#tablestyle {
  font-family: 'Manrope', sans-serif;
  /* border-radius: 1%; */
}

.site-footer {
  border-top: 7px solid pink;
  background-image: linear-gradient( #c40484, #610241);
  padding: 20px ;
  font-family: 'Courgette', cursive;
}

